.flow-chart {
  display: flex;
  height: 100vh;
  width: 100%;
}

.question-column {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
}

.answer-column {
  flex: 2;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  gap: 20px;
  overflow-y: auto;
}

/* Add these styles to your AnswerCard component's CSS */
.answer-card {
  width: calc(50% - 10px);
  height: 150px; /* Adjust this value as needed */
  overflow: hidden;
  border: 1px solid #ccc;
  padding: 10px;
  box-sizing: border-box;
}

.progress-bar {
  width: 100%;
  height: 4px; /* Adjust as needed */
  background-color: #e0e0e0; /* Light grey background */
}

.progress {
  height: 100%;
  background-color: white; /* Change this to white */
  transition: width 0.3s ease; /* Smooth transition for width changes */
}